.card {
	position: relative;
}
.card img {
	/* width: 100%; */
	display: block;
	/* border: 2px solid #fff; */
	/* border-radius: 6px; */
}
.card .front {
	transform: rotateY(90deg);
	transition: all ease-in 0.2s;
	position: absolute;
}
.flipped .front {
	transform: rotateY(0deg);
	transition-delay: 0.2s;
}
.card .back {
	transition: all ease-in 0.2s;
	transition-delay: 0.2s;
}
.card .back:hover {
	filter: brightness(1.3);
	transition-delay: 0s;
}
.card .back.locked {
	filter: brightness(0.7);
}
.flipped .back {
	transform: rotateY(90deg);
	transition-delay: 0s;
}
.disabled .front {
	opacity: 0.5;
	filter: brightness(0.3);
}
.flipped.small {
	margin-top: 37px;
}
