.App {
	max-width: 800px;
	/* margin: 40px auto; */
	position: absolute;
	width: 800px;
	height: 700px;
	background: linear-gradient(180deg, #310f46 0%, #0a0518 100%);
}
button {
	background: none;
	/* border: 2px solid #fff; */
	padding: 6px 12px;
	border-radius: 5px;
	color: #fff;
	font-weight: bold;
	cursor: default;
	border: 3px solid #371b48;
}
button:hover {
	border: 3px solid #ffa800;
	color: #fff;
}
.floor-deck {
	margin-top: 40px;
	display: grid;
	grid-template-columns: 140px 140px 140px 140px;
	grid-gap: 20px;
	margin-left: 90px;
}
.action-deck {
	/* height: 300px; */
	margin-top: 24px;
	/* margin-bottom: 40px; */
	display: grid;
	grid-template-columns: 64px 100px 64px;
	grid-gap: 20px;
	margin-left: 263px;
}
.top-bar {
	display: flex;
	justify-content: space-between;
	margin: 20px 20px;
}
.top-bar .health {
	display: flex;
	background: #371b48;
	height: 40px;
	width: 100px;
	border-radius: 5px;
}
.top-bar .status {
	background: #371b48;
	height: 40px;
	width: 300px;
	border-radius: 5px;
}
.top-bar .score {
	background: #371b48;
	display: flex;
	height: 40px;
	width: 100px;
	border-radius: 5px;
}
.top-bar p {
	margin: auto;
	line-height: 40px;
}
.top-bar img {
	scale: 0.7;
}
.modal {
	background: #0a0518b5;
	width: 100%;
	position: absolute;
	z-index: 1;
	height: 100%;
}
.modal-content {
	margin: 23% auto;
	background: #0a0518;
	width: 35%;
	height: 30%;
	padding: 1%;
	border-radius: 5px;
}
.modal-buttons {
	display: flex;
	flex-direction: column;
}
.modal-buttons button {
	width: 50%;
	margin: 5px auto;
}
.menu-button {
	right: 10px;
	position: absolute;
	bottom: 10px;
}
.monster-hp {
	left: 20px;
	position: absolute;
	bottom: 0px;
	color: white;
}
